*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.page{
    height: 100px;width: 100%;
    display: flex;align-items: center;justify-content:space-between;
    padding-inline: 50px;
    background-color: rgb(8, 8, 8);color: white;
    font-size: 3ch;
}
.page button{
    height: 50px;width: 250px;border-radius: 10px;background-color: aqua;
}
.page button:hover{
    opacity: 0.5;cursor: pointer;

}
.library{
    height: auto;width: 100%;
    display:flex;justify-content: space-evenly;align-items: center;
    flex-wrap: wrap;
    column-gap: 20px;
    text-align: center;
}
.box{
    height: auto;width: 400px;
    border: 2px solid black;
    text-align: center;
    overflow: hidden;
    font-size: 2ch;
    background-color: black;color: wheat;border-radius: 20px;
}
.box button{
    height: 50px;width: 250px;border-radius: 10px;background-color: rgb(160, 224, 9);
}
.box button:hover{
    opacity: 0.6;cursor: pointer;
}
.video-box{
    height: 530px;width: 500px;
    border: 2px solid black;
    text-align: center;
    overflow: hidden;
    font-size: 2ch;
    background-color: rgba(0, 0, 0, 0.534);color: wheat;border-radius: 20px;
}